.card {
    margin-bottom: 102px;
}
.info-card {
    max-width: 400px;
    margin: auto;
    position: relative;
    padding: 0 50px;
    
    @include m (first) {
        &::after {
            content: '';
            position: absolute;
            width: 29px;
            height: 125px;
            background: url(#{$i}Arrow.svg) no-repeat;
            background-size: 100%;
            top: 185px;
            right: 62px;
            transform: rotate(5deg);
        }
    }
    @include m (second) {
        &::after {
            content: '';
            position: absolute;
            width: 29px;
            height: 125px;
            background: url(#{$i}Arrow.svg) no-repeat;
            background-size: 100%;
            top: 185px;
            right: 62px;
            
        }
    }
    @include e (img) {
        width: 77px;
        height: 58px;
        background: url(#{$i}info-card1.svg) no-repeat;
        background-size: 100%;
        margin-bottom: 19px;
        &::before {
            content: '';
            position: absolute;
            width: 78px;
            height: 78px;
            background-color: #FAEEEE;
            border-radius: 22px;
            z-index: -1;
            top: 43px;
            left: 20px;
        }
        @include m (second) {
            background: url(#{$i}info-card2.svg) no-repeat;
            &::before {
                background-color: #E5F7E5;
            }
        }
        @include m (third) {
            background: url(#{$i}info-card3.svg) no-repeat;
            &::before {
                background-color: #EAF8F9;
            }
        }
    }

    h3 {
        @include logotext (600, 20px, 27px);
        text-align: center;
        margin-bottom: 21px;
    }
    p {
        @include logotext (400, 14px, 26px);
        margin-bottom: 49px;
        font-family: $QFontFamily;
    
    }
}