.featured-card {
    max-width: 310px;
    margin: auto;
    margin-bottom: 45px;
    @include e (item) {
        width: 300px;
        background-color: #F5F7FA;
        margin: auto;
        margin-bottom: 25px;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
        font-family: $QFontFamily;
        @include e (img) {
            width: 300px;
            height: 250px;
            background: url(#{$i}gray.png) no-repeat;
            border-radius: 10px;
            margin-bottom: 33px;
        }
        h4 {
            @include logotext (600, 24px, 33px);
            margin-bottom: 5px;
            margin-left: 13px;
        }
        @include e (inf-first) {
            @include logotext (400, 14px, 13px);
            margin-bottom: 17px;
            margin-left: 14px;
        }
        @include e (inf-second) {
            @include logotext (400, 18px, 17px);
            margin-left: 14px;
            margin-bottom: 15px;
            span {
                color: #E63A28;
            }
        }
        @include e (inf-third) {
            @include logotext (400, 12px, 11px);
            position: relative;
            margin-left: 100px;
            margin-bottom: 28px;
            &::before {
                content: '';
                position: absolute;
                width: 81px;
                height: 18px;
                background: url(#{$i}stars.svg) no-repeat;
                top: -5px;
                left: -85px;
            }
        }
        @include e (inf-line) {
            width: 280px;
            height: 1px;
            background-color: #DDDDDD;
            margin: auto;
            margin-bottom: 25px;
        }
        @include e (inf-fourth) {
            @include logotext (700, 16px, 20px);
            margin-left: 40px;
            margin-bottom: 13px;
            position: relative;
            &::before {
                content: '';
                position: absolute;
                width: 16px;
                height: 18px;
                background: url(#{$i}icon-card4.svg) no-repeat;
                left: -24px;
            }
        }
        @include e (inf-fifth) {
            @include logotext (400, 14px, 13px);
            margin-left: 40px;
            padding-bottom: 41px;
            position: relative;
            &::before {
                content: '';
                position: absolute;
                width: 16px;
                height: 18px;
                background: url(#{$i}icon-card5.svg) no-repeat;
                left: -22px;
            }
        }
    }
}
.featured-btn {
    text-align: center;
    margin-bottom: 122px;
    @include e (item) {
        padding: 20px 42px;
        border-radius: 30px;
        background-color: $btnColor;
        color: $white;
        font-weight: 700;
        font-size: 18px;
        line-height: 22px;
        text-transform: uppercase;
    }
}