.search-form {
    position: relative;
    border: 4px solid rgba(245, 245, 245, 0.4);
    border-radius: 33px;
    background: $white;
    margin-bottom: 12px;
    max-width: 760px;
    
    @include e (inp) {
        width: 220px;
        padding: 8px 0;
        padding-left: 30px;
        
    }
    @include e (ico) {
        content: "";
        display: block;
        background: url(#{$i}Search.svg) no-repeat;
        position: absolute;
        width: 13px;
        height: 14px;
        top: 8.8px;
        left: 10px;
    }   
}
.search_block {
    margin-bottom: 48px;
}
.search-btn {
    text-align: center;
    margin-bottom: 54px;
    @include e (item) {
        padding: 18px 52px;
        border-radius: 28px;
        background-color: $btnColor;
        color: $white;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        text-transform: uppercase;
    }
}

.popular-search {
    @include e (ico) {
        display: flex;
        justify-content: center;
        margin-bottom: 18px;
        @include e (bild) {
            width: 39px;
            height: 40px;
            background: url(#{$i}bild.svg) no-repeat;
            margin-right: 7px;
            // background-position: -10px -10px;
        }
        @include e (cars) {
            width: 39px;
            height: 40px;
            background: url(#{$i}car.svg) no-repeat;
            margin-right: 7px;
            // background-position: -69px -10px;
        }
        @include e (shop) {
            width: 39px;
            height: 40px;
            background: url(#{$i}shop.svg) no-repeat;
            // background-position: -128px -10px;
        }
    }
    p {
        text-align: center;
        @include logotext (700, 16px, 20px);
        font-family: $QFontFamily;
    }
    a {
        text-decoration: underline;
        color: #E63A28;
    }
}