.menu {
    padding-top: 55px;
    margin: 0 100px 161px 100px;
    @include e (ico) {
        display: none;
    }
    @include e (item) {
        @include logotext (400, 16px, 15px);
        display: flex;
        align-items: center;
        list-style: none;
        margin-left: auto;
        padding-left: 0;
        &_row::after {
            content: "";
            display: inline-block;
            background: url(#{$i}row.svg) no-repeat;
            width: 16px;
            height: 15px;
            margin-left: 7px;
            vertical-align: bottom;
        }
        @include e (sing) {
            text-decoration: underline;
            padding-top: -5px;
            &::before {
                content: "";
                display: inline-block;
                background: url(#{$i}sing.svg) no-repeat;
                width: 18px;
                height: 16px;
                margin-right: 9px;
                vertical-align: bottom;  
            }
        }
        li {
            font-family: $QFontFamily;
            margin-left: 60px;
            &:first-child{
                margin-left: 0;
            }
        }
        @include e (btn) {
            background: #2B5AE1;
            font-family: $QFontFamily;
            color: $white;
            padding: 13px 30px;
            border-radius: 25px;
            width: 100%;
            font-weight: 700;
            font-size: 18px;
            line-height: 22px;
            &:hover {
                cursor: pointer;
            }
        }
        @include e (lang) {
            span {
                margin-left: 7px;
            }
            &::after {
                content: "";
                display: inline-block;
                background: url(#{$i}row.svg) no-repeat;
                width: 16px;
                height: 15px;
                margin-left: 9px;
                vertical-align: bottom;  
            }
        }
    }
}



