
.social-box {
    max-width: 1150px;
    flex-wrap: nowrap;
    justify-content: space-between;
}
.social-first {
    @include e (logo) {
        margin-left: 0;
    }
    @include e (text) {
        margin-left: 0;
    }
    @include e (conect) {
        margin-left: 0;
    }
}
.social__second {
    display: block;
    margin-left: 0;
    h4 {
        @include logotext (600, 24px, 30px);
        padding-top: 48px;
        margin-bottom: 19px;
        
    }
    ul {
        list-style: none;
        padding-left: 0;
        @include logotext (400, 16px, 34px);

    }
}
.social__third {
    margin-left: 0;
}
.social_linked {
    display: block;
}
.social__forth {
    padding-top: 48px;
}
.social__fifth {
    @include e (link) {
        justify-content: flex-start;
        @include e (item) {
            @include m (twiter) {
                background: url(#{$i}sprite2.png);
                background-position: -10px -10px;
                &:hover {
                    background-position: -330px -10px;
                }
            }
            @include m (facebook) {
                background: url(#{$i}sprite2.png);
                background-position: -74px -10px;
                &:hover {
                    background-position: -458px -10px;
                }
            }
            @include m (inst) {
                background: url(#{$i}sprite2.png);
                background-position: -138px -10px;
                &:hover {
                    background-position: -586px -10px;
                }
            }
            @include m (pint) {
                background: url(#{$i}sprite2.png);
                background-position: -202px -10px;
                &:hover {
                    background-position: -394px -10px;
                }
            }
            @include m (youtube) {
                background: url(#{$i}sprite2.png);
                background-position: -266px -10px;
                &:hover {
                    background-position: -522px -10px;
                }
            }
            }
        }
 }

