.app-bg {
    background-color: #7828E6 ;
    margin-bottom: 70px;
}
.app-blok {
    @include e (text) {
        max-width: 733px;
        margin: auto;
        h2 {
            @include logotext (700, 60px, 82px);
            color: $white;
            margin-bottom: 30px;
            padding-top: 70px;
        }
        p {
            @include logotext (400, 24px, 38px);
            color: $white;
            margin-bottom: 0;
        }
    }
    @include e (img) {
        height: 300px;
        max-width: 455px;
        background: url(#{$i}phone.png) no-repeat;
        background-size: cover;
        background-position: center;
        margin: auto;
        margin-bottom: 30px;
    }
    @include e (market) {
        display: block;
        justify-content: center;
        padding-bottom: 27px;
        padding-top: 60px;
        order: -1;
        position: relative;
        &::after {
            content: '';
            position: absolute;
            width: 555px;
            height: 386px;
            background: url(#{$i}phone.png) no-repeat;
            background-size: cover;
            top: -56px;
            left: 180px;
        }
        @include e (google) {
            width: 220px;
            height: 65px;
            background: url(#{$i}google.jpg) no-repeat;
            background-size: 100%;
            margin-right: 0;
            margin-bottom: 40px;
            border-radius: 10px;
            background-position: 3% 30%;
        }
        @include e (apple) {
            width: 220px;
            height: 65px;
            background: url(#{$i}apple.jpg);
            background-size: 100%;
            margin-right: 0;
            border-radius: 10px;
            background-position: 6% 30%;
        }
    }
}
.app-blok-img {
    display: flex;
    max-width: 681px;
    margin: auto;
}

   

