.social {
    background: #EEF3FA; 
    font-family: $QFontFamily;
}
.social-box {
    max-width: 320px;
    margin: auto;
}
.social-first {
    @include e (logo) {
        display: flex;
        padding-top: 36px;
        margin-left: 35px;
        margin-bottom: 32px;
        @include e (img) {
            background: url(#{$i}logo.svg) no-repeat;
            background-size: 100%;
            width: 54px;
            height: 54px;
            margin-right: 14px;
            margin-top: 6px;
        }
        @include e (text) {
            h3 { 
                @include logotext (800, 36px, 49px);
                margin-bottom: -5px;
            }
            p {
                @include logotext (300, 14px, 13px);
            }
        }
    }
    @include e (text) {
        @include logotext (400, 16px, 26px);
        max-width: 284px;
        margin-left: 35px;
        margin-bottom: 19px;
    }
    @include e (conect) {
        @include logotext (400, 20px, 26px);
        max-width: 284px;
        margin-left: 35px;
        margin-bottom: 51px;
        span {
            font-weight: bold;
        }
    }
}
.social__second {
    display: none;
}
.social__third {
    display: none;
}
.social__forth {
    h4 {
        @include logotext (600, 24px, 30px);
        margin-left: 35px;
        margin-bottom: 19px;
    }
    p {
        @include logotext (400, 16px, 34px);
        margin-left: 35px;
        margin-bottom: 44px;
    }
    @include e (market) {
        display: flex;
        justify-content: center;
        margin-bottom: 50px;
        @include e (google) {
            width: 42px;
            height: 42px;
            background: url(#{$i}google.jpg);
            border: 1px solid #B9B9B9;
            border-radius: 5px;
            background-position: 3% 30%;
            margin-right: 45px;
        }
        @include e (apple) {
            width: 42px;
            height: 42px;
            background: url(#{$i}apple.jpg);
            border: 1px solid #B9B9B9;
            border-radius: 5px;
            background-position: 6% 30%;
        }
    }

}
.social__fifth {
    h4 {
        @include logotext (600, 24px, 30px);
        margin-bottom: 22px;
        margin-left: 35px;
    }
    @include e (link) {
        display: flex;
        justify-content: center;
        padding-bottom: 37px;
        @include e (item) {
            width: 44px;
            height: 44px;
            margin-right: 8px;
            &:nth-last-child(1){
                margin-right: 0px;
            }
            @include m (twiter) {
                background: url(#{$i}sprite2.png);
                background-position: -10px -10px;
            }
            @include m (facebook) {
                background: url(#{$i}sprite2.png);
                background-position: -74px -10px;
            }
            @include m (inst) {
                background: url(#{$i}sprite2.png);
                background-position: -138px -10px;
            }
            @include m (pint) {
                background: url(#{$i}sprite2.png);
                background-position: -202px -10px;
            }
            @include m (youtube) {
                background: url(#{$i}sprite2.png);
                background-position: -266px -10px;
            }
        }
    }
   
}