.sponsor {
    h2 {
        @include logotext (700, 24px, 33px);
        text-align: center;
        margin-bottom: 51px;
    }
}
.sponsor-card {
    max-width: 300px;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    
    margin-bottom: 11px;
    @include e (item) {
        width: 138px;
        height: 103px;
        background: url(#{$i}spomsor1.png) no-repeat;
        background-position: center;
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: 5px;
        margin-right: 24px;
        margin-bottom: 43px;
        &:nth-child(2n) {
            margin-right: 0;
        }
        @include m (second) {
            background: url(#{$i}sponsor2.png) no-repeat;
            background-position: center;
        }
        @include m (third) {
            background: url(#{$i}sponsor3.png) no-repeat;
            background-position: center;
        }
        @include m (forth) {
            background: url(#{$i}sponsor4.png) no-repeat;
            background-position: center;
        }
        @include m (fifth) {
            background: url(#{$i}sponsor5.png) no-repeat;
            background-position: center;
        }
    }

}