.search-form {
    position: relative;
    border: 8px solid rgba(230, 230, 230, 0.6);
    margin-bottom: 18px;
    border-radius: 48px;
    @include e (inp) {
        font-size: 1rem;
        width: 550px;
        padding: 24px 0;
        padding-left: 64px; 
    }
    @include e (ico) {
        background-size: 100%;
        width: 19px;
        height: 25px;
        top: 23px;
        left: 30px;
    }   
}
.search-btn {
    margin-bottom: 57px;
}

.popular-search {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 200px;
    @include e (ico) {
        margin-bottom: 0;
        @include e (bild) {
            width: 50px;
            height: 50px;
            background: url(#{$i}bild.svg) no-repeat;
            background-size: 100%;
            margin-right: 9px;
            // background-position: -10px -10px;
        }
        @include e (cars) {
            width: 50px;
            height: 50px;
            background: url(#{$i}car.svg) no-repeat;
            background-size: 100%;
            margin-right: 9px;
            // background-position: -69px -10px;
        }
        @include e (shop) {
            width: 50px;
            height: 50px;
            background: url(#{$i}shop.svg) no-repeat;
            background-size: 100%;
            
            // background-position: -128px -10px;
        }
    }
    @include e (text) {
        order: -1;
        margin-left: 33px;
        margin-right: 33px;
    }
    @include e (link) {
        margin-left: 21px;
    }
    p {
        text-align: center;
        @include logotext (700, 16px, 20px);
    }
    a {
        text-decoration: underline;
        color: #E63A28;
    }
}