.rental-bg {
    position: relative;
    &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 937px;
        background-color: #F1FDFB;
        top: 0px;
        z-index: -2;
    }
    &::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 150px;
        background-color: #F1FDFB;
        top: -73px;
        z-index: -2;
        border-radius: 100%;
    }
}
.rental-info {

    margin: auto;
    margin-bottom: 109px;
    h2 {
        @include logotext (700, 32px, 44px);
        margin: 0 40px 33px 25px;
    }
    @include e (card) {
        max-width: 280px;
        margin: auto;
        @include e (img) {
            width: 89px;
            height: 89px;
            background: url(#{$i}rental-ico1.png) no-repeat;
            margin: auto;
            margin-bottom: 15px;
            @include m (second) {
                background: url(#{$i}rental-ico2.png) no-repeat;
            }
            @include m (third) {
                background: url(#{$i}rental-ico3.png) no-repeat;
            }
        }
        h4 {
            @include logotext (700, 20px, 27px);
            text-align: center;
            margin-bottom: 10px;
        }
        p {
            @include logotext (400, 14px, 17px);
            font-family: $QFontFamily;
            text-align: center;
            margin-bottom: 44px;
        }
    }
}