.menu {
    display: flex;
    padding-top: 27px;
    margin-bottom: 86px;
    @include e (item) {
        display: none;
    }
    @include e (ico) {
        background: url(#{$i}menu.svg) no-repeat;
        background-size: 100%;
        width: 24px;
        height: 19px;
        margin-left: auto;
        margin-top: 4px;
    }
    @include e (logo) {
        display: flex;
        @include e (img) {
            background: url(#{$i}logo.svg) no-repeat;
            background-size: 100%;
            width: 28.7px;
            height: 28.7px;
            margin-right: 6.3px;
            margin-top: 6px;
        }
        @include e (text) {
            h3 { 
                @include logotext (800, 22px, 30px);
                margin-bottom: -5px;
            }
            p {
                @include logotext (300, 10px, 12px);
                font-family: $QFontFamily;
            }
        }
    }
}

