.reviews-card {
    width: 300px;
    margin: auto;
    background: #FFFFFF;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    margin-bottom: 50px;
    font-family: $QFontFamily;
    @include e (img) {
        width: 100px;
        height: 100px;
        background: url(#{$i}avatar.png);
        margin: 27px auto 34px;
    }
    @include e (text) {
        @include logotext (400, 14px, 26px);
        margin: 0 15px 15px 19px;
    }
    @include e (name) {
        @include logotext (700, 18px, 22px);
        margin-left: 21px;
        margin-bottom: 14px;
        position: relative;
        &::after {
            content: '';
            position: absolute;
            width: 81px;
            height: 18px;
            background: url(#{$i}stars.svg) no-repeat;
            margin-left: 32px;
        }
    }
    @include e (rental) {
        @include logotext (400, 14px, 13px);
        margin-left: 21px;
        margin-bottom: 25px;
    }
}