
.social-box {
    max-width: 768px;
    display: flex;
    flex-wrap: wrap;
}
.social-first {
    @include e (logo) {
        margin-left: 74px;
    }
    @include e (text) {
        margin-left: 74px;
    }
    @include e (conect) {
        margin-left: 74px;
        margin-bottom: 98px;
    }
}
.social__second {
    display: none;
}
.social_linked {
    display: flex;
}
.social__third {
    display: block;
    margin-left: 100px;
    h4 {
        @include logotext (600, 24px, 30px);
        padding-top: 48px;
        margin-bottom: 19px;
        
    }
    ul {
        list-style: none;
        padding-left: 0;
        @include logotext (400, 16px, 34px);

    }
}
.social__forth {
    h4 {
        margin-left: 74px;
    }
    p {
        margin-left: 74px;
        margin-bottom: 13px;
    }
    @include e (market) {
        display: flex;
        justify-content: start;
        margin-left: 74px;
        margin-bottom: 50px;
        @include e (google) {
            width: 154px;
            height: 46px;
            margin-right: 11px;
        }
        @include e (apple) {
            width: 154px;
            height: 46px;
        }
    }

}
.social__fifth {
    margin-left: 62px;
    h4 {
        margin-left: 0;
    }
}