.sponsor {
    h2 {
        margin-bottom: 85px;
    }
}
.sponsor-card {
    max-width: 670px;
    margin-bottom: 5px;
    @include e (item) {
        width: 200px;
        height: 150px;
        margin-right: 35px;
        margin-bottom: 60px;
        &:nth-child(2n) {
            margin-right: 35px;
        }
        &:nth-child(3n) {
            margin-right: 0px;
        }
    }
}