.main {
    margin-left: 40px;
    ::after {
        content: '';
        position: absolute;
        width: 115px;
        height: 150px;
        background: url(#{$i}deco.png) no-repeat;
        background-size: 100%;
        top: 164px;
        right: 16px;
        z-index: -1;
    }
    & h1 {
        @include logotext (700, 36px, 49px);
        margin-bottom: 21px;
    }
    & p {
        @include logotext (300, 16px, 19px);
        font-family: $QFontFamily;
        margin-bottom: 34px;

    }
}