.menu {
    padding-top: 55px;
    margin-bottom: 114px;
    margin-left: 30px;
    @include e (logo) {
        @include e (img) {
            width: 54px;
            height: 54px;
            margin-right: 6.3px;
            margin-top: 6px;
        }
        @include e (text) {
            h3 { 
                @include logotext (800, 36px, 49px)
            }
            p {
                @include logotext (300, 14px, 13px)
            }
        }
    }
}

