.featured-card {
    max-width: 1140px;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 34px;
    @include e (item) {
        width: 360px;
        margin-bottom: 36px;
        @include e (img) {
            width: 360px;
        }
        h4 {
            margin-left: 32px;
        }
        @include e (inf-first) {
            margin-left: 33px;
        }
        @include e (inf-second) {
            margin-left: 33px;
        }
        @include e (inf-third) {
            margin-left: 119px;
        }
        @include e (inf-line) {
            width: 360px;
        }
        @include e (inf-fourth) {
            margin-left: 59px;
        }
        @include e (inf-fifth) {
            margin-left: 59px;
        }
    }
}