.info {
    position: relative;
    @include e (chapter) {
        margin-bottom: 40px;
    }
    @include e (head) {
        @include logotext (700, 60px, 82px);
        margin-bottom: 8px;
    }
    @include e (text) {
        margin-bottom: 84px;
        max-width: 600px;
        position: relative;
       p {
        @include logotext (400, 16px, 26px);
        padding: 0 10px;
       }
       @include m (dots) {
        &::before {
            content: '';
            position: absolute;
            width: 215px;
            height: 225px;
            background: url(#{$i}dots.svg);
            top: -37px;
            right: -83px;
            z-index: -1;
       }
       
    }
    }
    @include m (popular) {
        position: relative;
        &::before {
            height: 1100px;
        }
        &::after {
            height: 300px;
            top: -142px;
        }
    }
}
