.card {
    display: flex;
    flex-wrap: nowrap;
    max-width: 1150px;
    margin: auto;
    margin-bottom: 200px;
}
.info-card {
    max-width: 385px;
    margin-left: 0;
    @include m (first) {
        &::after {
            top: 157px;
            right: 21px;
            transform: rotate(-85deg) scaleX(-1);
            
        }
    }
    @include m (second) {
        &::after {
            top: 157px;
            right: 0px;
            transform: rotate(-85deg) scaleX(-1);
            
        }
    }
}