.sponsor {
    h2 {
        margin-bottom: 64px;
    }
}
.sponsor-card {
    max-width: 1140px;
    margin-bottom: 120px;
    @include e (item) {
        width: 200px;
        height: 150px;
        margin-right: 35px;
        margin-bottom: 0px;
        &:nth-child(2n) {
            margin-right: 35px;
        }
        &:nth-child(3n) {
            margin-right: 35px;
        }
        &:nth-last-child(1) {
            margin-right: 0px;
        }
    }
}