.card {
    display: flex;
    flex-wrap: wrap;
    max-width: 800px;
    margin: auto;
    margin-bottom: 200px;
}
.info-card {
    max-width: 385px;
    margin-left: 0;
    @include m (first) {
        &::after {
            top: 157px;
            right: 21px;
            transform: rotate(-85deg) scaleX(-1);
            
        }
    }
    @include m (second) {
        &::after {
            top: 264px;
            right: 295px;
            transform: rotate(10deg);
            
        }
    }
}