$prefix: prefix- !default;
$baseFontSize: 16px;
$baseFontFamily: "Nunito", sans-serif;
$LFontFamily: 'Lato', sans-serif;
$QFontFamily: 'Quicksand', sans-serif;

$mediumFontWeight: 500;

$mobilePadding: (12px / $baseFontSize) * 1rem;
$desktopPadding: 1rem;

// Paths

$i: "../i/";
$fontsPath: "../fonts/";
$usersFilesPath: "../userfiles/";

// Colors
$black: #000000;
$white: #ffffff;
$textColor: #222222;
$btnColor: #E63A28;
$blue: #0d6efd !default;
$indigo: #6610f2 !default;
$purple: #6f42c1 !default;
$pink: #d63384 !default;
$red: #dc3545 !default;

$colors: (
  "black": $black,
  "white": $white,
  "blue": $blue,
  "indigo": $indigo,
  "purple": $purple,
  "pink": $pink,
  "red": $red,
) !default;
