.app-bg {
    background-image: url(#{$i}bg-app.png);
    background-color: #7828E6 ;
    margin-bottom: 70px;
}
.app-blok {
    @include e (text) {
        max-width: 455px;
        margin: auto;
        h2 {
            @include logotext (700, 36px, 49px);
            color: $white;
            margin-bottom: 35px;
            padding-top: 25px;
        }
        p {
            @include logotext (400, 20px, 32px);
            font-family: $QFontFamily;
            color: $white;
            margin-bottom: 30px;
        }
    }
    @include e (img) {
        height: 300px;
        max-width: 455px;
        background: url(#{$i}phone.png) no-repeat;
        background-size: cover;
        background-position: center;
        margin: auto;
        margin-bottom: 30px;
    }
    @include e (market) {
        display: flex;
        justify-content: center;
        padding-bottom: 27px;
        @include e (google) {
            width: 42px;
            height: 42px;
            background: url(#{$i}google.jpg);
            border-radius: 5px;
            background-position: 3% 30%;
            margin-right: 45px;
        }
        @include e (apple) {
            width: 42px;
            height: 42px;
            background: url(#{$i}apple.jpg);
            border-radius: 5px;
            background-position: 6% 30%;
        }
    }
}

   

