.reviews-card {
    width: 515px;
    margin: auto;
    background: #FFFFFF;
    border: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: 0px 0px 50px rgba(18, 53, 87, 0.12);
    border-radius: 10px;
    margin-bottom: 50px;
    position: relative;
    @include e (img) {
        margin: 0;
        position: absolute;
        top: 50px;
        left: -30px;
    }
    @include e (text) {
        @include logotext (400, 16px, 26px);
        margin: 50px 46px 21px 111px;
    }
    @include e (name) {
        margin-left: 111px;
    }
    @include e (rental) {
        margin-left: 111px;
        margin-bottom: 52px;
    }
}