.rental-bg {
   position: relative;
   &::after {
       content: '';
       position: absolute;
       width: 100%;
       height: 781px;
       background-color: #F1FDFB;
       top: -47px;
       z-index: -2;
   }
   &::before {
       content: '';
       position: absolute;
       width: 100%;
       height: 150px;
       background-color: #F1FDFB;
       top: -75px;
       z-index: -2;
       border-radius: 100%;
   }
}
.rental {
 max-width: 1200px;
 display: flex;
 margin: auto;
 margin-bottom: 139px;
 @include e (img) {
    background: url(#{$i}people-group.png) no-repeat;
    background-size: 100%;
    background-position: center;
    width: 668px;
 }
}
.rental-info {
   margin-bottom: 0;
}