* {
  margin: 0;
}

html {
  @include getCssVar(font-size);
}

body {
  @include getCssVar(font-family);
  font-size: 1rem;
}

.head-blok {
  position: relative;
  padding: 0 22px;
  &::before {
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    height: 600px;
    left: 0;
    top: 0;
    background: #EEF3FA;
    z-index: -2;
  }
  &::after {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100px;
    background: #EEF3FA;
    z-index: -2;
    border-radius: 100%;
    bottom: -34px;
    left: 0;
  }
}


