.search {
    position: relative;
}
.search-form {
    position: relative;
    border: 8px solid rgba(230, 230, 230, 0.6);
    margin-bottom: 35px;
    border-radius: 48px;
    @include e (inp) {
        font-size: 1rem;
        width: 470px;
        padding: 24px 0;
        padding-left: 64px; 
    }
    @include e (ico) {
        background-size: 100%;
        width: 19px;
        height: 25px;
        top: 23px;
        left: 30px;
    }   
}
.search-btn {
    position: absolute;
    right: 19px;
    bottom: 15px;
    margin: 0;
    @include e (item) {
        &:hover {
            cursor: pointer;
        }
    }
}

.popular-search {
    justify-content: flex-start;
    margin-bottom: 282px;
    @include e (ico) {
        @include e (bild) {
            position: relative;
            &:hover {
                background: url(#{$i}bild_2.svg) no-repeat;
                background-size: 100%;
                &::after {
                    content: '';
                    display: block;
                    position: absolute;
                    width: 80px;
                    height: 36px;
                    background: url(#{$i}Button.svg);
                    top: 55px;
                    right: -14px;
                  }
            }
        }
        @include e (cars) {
            position: relative;
            &:hover {
                background: url(#{$i}car_2.svg) no-repeat;
                background-size: 100%;
                &::after {
                    content: '';
                    display: block;
                    position: absolute;
                    width: 80px;
                    height: 36px;
                    background: url(#{$i}Button.svg);
                    top: 55px;
                    right: -14px;
                  }
            }
        }
        @include e (shop) {
            position: relative;
            &:hover {
                background: url(#{$i}shop_2.svg) no-repeat;
                background-size: 100%;
                &::after {
                    content: '';
                    display: block;
                    position: absolute;
                    width: 80px;
                    height: 36px;
                    background: url(#{$i}Button.svg);
                    top: 55px;
                    right: -14px;
                  }
            }
        }
    }
    @include e (text) {
        order: -1;
        margin-left: 33px;
        margin-right: 33px;
    }
    @include e (link) {
        margin-left: 21px;
    }
    p {
        text-align: center;
        @include logotext (700, 16px, 20px);
    }
    a {
        text-decoration: underline;
        color: #E63A28;
    }
}


