.popular-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 160px;
    @include e (item) {
        width: 250px;
        margin-bottom: 25px;
        background-color: $white;
        box-shadow: 0px 0px 25px rgba(43, 15, 64, 0.08);
        border-radius: 10px;
        @include e (img) {
            width: 98px;
            height: 81px;
            background: url(#{$i}popular1.png);
            background-position: center;
            background-size: contain;
            margin: auto;
            margin-top: 61px;
            margin-bottom: 42px;
            @include m (second) {
                background: url(#{$i}popular2.png) no-repeat;
                background-position: center;
                background-size: contain;
            }
            @include m (third) {
                background: url(#{$i}popular3.png) no-repeat;
                background-position: center;
                background-size: contain;
            }
            @include m (fourth) {
                background: url(#{$i}popular4.png) no-repeat;
                background-position: center;
                background-size: contain;
            }
            @include m (fifth) {
                background: url(#{$i}popular5.png) no-repeat;
                background-position: center;
                background-size: contain;
            }
            @include m (sixth) {
                background: url(#{$i}popular6.png) no-repeat;
                background-position: center;
                background-size: contain;
            }
        }
        h4 {
            @include logotext (600, 24px, 33px);
            margin-bottom: 11px;
            text-align: center;
        }
        p {
            @include logotext (400, 16px, 15px);
            margin-bottom: 11px;
            text-align: center;
            font-family: $QFontFamily;
        }
    }
}
