.info {
    @include e (chapter) {
        position: relative;
        max-width: 332px;
        margin: auto;
        h4 {
            @include logotext (400, 16px, 15px);
            text-align: center;
            margin-bottom: 21px;
            font-family: $QFontFamily;
        }
        &::before {
            content: '';
            position: absolute;
            width: 33px;
            height: 1px;
            background-color: #E63A28;
            left: 15px;
            top: 6px;
        }
        &::after {
            content: '';
            position: absolute;
            width: 33px;
            height: 1px;
            background-color: #E63A28;
            right: 15px;
            top: 6px;
        }
    }
    @include e (head) {
        text-align: center;
        @include logotext (700, 36px, 49px);
        margin-bottom: 24px;
    }
    @include e (text) {
        min-width: 267px;
        padding: 0 22px;
        text-align: center;
        margin-bottom: 31px;
       p {
        @include logotext (400, 14px, 26px);
        padding: 0 10px;
        font-family: $QFontFamily;
       }

    }
    @include m (popular) {
        position: relative;
        &::before {
            content: '';
            width: 100%;
            height: 1900px;
            position: absolute;
            background-color: #FFFDF5;
            z-index: -2;
        }
        &::after {
            content: '';
            width: 100%;
            height: 100px;
            border-radius: 100%;
            position: absolute;
            background-color: #FFFDF5;
            z-index: -2;
            top: -87px;
        }
    }

}