.rental-bg {
    position: relative;
    &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 697px;
        background-color: #F1FDFB;
        top: -47px;
        z-index: -2;
    }
    &::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 150px;
        background-color: #F1FDFB;
        top: -75px;
        z-index: -2;
        border-radius: 100%;
    }
}

.rental-info {
    max-width: 624px;
    margin: auto;
    margin-bottom: 47px;
    h2 {
        @include logotext (700, 48px, 65px);
        margin: 0;
        margin-bottom: 40px;
    }
    @include e (card) {
        display: flex;
        max-width: 550px;
        margin: 0;
        @include e (img) {
            width: 89px;
            height: 89px;
            margin: 0;  
        }
        @include e (text) {
            max-width: 383px;
            margin-left: 28px;
            margin-bottom: 55px;
            h4 {
                @include logotext (700, 20px, 27px);
                text-align: start;
                margin-bottom: 26px;
            }
            p {
                @include logotext (400, 14px, 17px);
                text-align: start;
                margin-bottom: 0;
            }
        }
        
    }
}